export const supabase = createClient(supabaseUrl, supabaseAnonKey)

export interface BlogPost {
  id: number
  title: string
  content: string
  created_at: string
  author_name: string
  user_id: string  // ID –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  likes_count: number
  tags: string[]
}

export const blogApi = {
  // –ü–æ–ª—É—á–∏—Ç—å –ø–æ—Å—Ç—ã —Å —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏–µ–π
  async getPosts(tag?: string): Promise<BlogPost[]> {
    try {
      let query = supabase
        .from('posts')
        .select('*')
        .order('created_at', { ascending: false })

      if (tag) {
        query = query.contains('tags', [tag])
      }

      const { data, error } = await query

      if (error) {
        console.error('Error fetching posts:', error)
        return []
      }

      return data as BlogPost[]
    } catch (err) {
      console.error('Unexpected error:', err)
      return []
    }
  },

  // –°–æ–∑–¥–∞—Ç—å –ø–æ—Å—Ç –æ—Ç –∏–º–µ–Ω–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  async createPost(
    title: string, 
    content: string, 
    userId: string,
    userName: string,
    tags: string[] = []
  ): Promise<BlogPost | null> {
    try {
      // –ò–∑–≤–ª–µ–∫–∞–µ–º —Ö–µ—à—Ç–µ–≥–∏ –∏–∑ —Ç–µ–∫—Å—Ç–∞
      const hashtags = content.match(/#[\w–∞-—è–ê-–Ø—ë–Å]+/g) || []
      const contentTags = hashtags.map(tag => tag.slice(1).toLowerCase())
      
      // –û–±—ä–µ–¥–∏–Ω—è–µ–º —Ç–µ–≥–∏
      const allTags = [...new Set([...tags, ...contentTags])]

      const postData = {
        title: title.trim(),
        content: content.trim(),
        author_name: userName,
        user_id: userId,
        likes_count: 0,
        tags: allTags
      }

      const { data, error } = await supabase
        .from('posts')
        .insert([postData])
        .select()
        .single()

      if (error) {
        console.error('Error creating post:', error)
        return null
      }

      return data as BlogPost
    } catch (err) {
      console.error('Unexpected error:', err)
      return null
    }
  },

  // –õ–∞–π–∫ –ø–æ—Å—Ç–∞
  async likePost(postId: number): Promise<BlogPost | null> {
    try {
      const { data: post } = await supabase
        .from('posts')
        .select('likes_count')
        .eq('id', postId)
        .single()

      if (!post) return null

      const { data, error } = await supabase
        .from('posts')
        .update({ likes_count: (post.likes_count || 0) + 1 })
        .eq('id', postId)
        .select()

      if (error) {
        console.error('Error liking post:', error)
        return null
      }

      return data[0] as BlogPost
    } catch (err) {
      console.error('Unexpected error:', err)
      return null
    }
  },

  // –ü–æ–ª—É—á–∏—Ç—å –ø–æ—Å—Ç—ã –∫–æ–Ω–∫—Ä–µ—Ç–Ω–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  async getUserPosts(userId: string): Promise<BlogPost[]> {
    try {
      const { data, error } = await supabase
        .from('posts')
        .select('*')
        .eq('user_id', userId)
        .order('created_at', { ascending: false })

      if (error) {
        console.error('Error fetching user posts:', error)
        return []
      }

      return data as BlogPost[]
    } catch (err) {
      console.error('Unexpected error:', err)
      return []
    }
  }
};