"use client";

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { blogApi, BlogPost } from '@/lib/supabase';
import { authService } from '@/lib/auth-service';
import Link from 'next/link';
import './blog.css';

export default function BlogPage() {
  const router = useRouter();
  const [posts, setPosts] = useState<BlogPost[]>([]);
  const [loading, setLoading] = useState(true);
  const [newPost, setNewPost] = useState({
    title: '',
    content: '',
    tagInput: ''
  });
  const [tags, setTags] = useState<string[]>([]);
  const [submitting, setSubmitting] = useState(false);
  const [searchTag, setSearchTag] = useState('');
  const [currentUser, setCurrentUser] = useState(authService.getCurrentUser());

  // –ü—Ä–æ–≤–µ—Ä—è–µ–º –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—é
  useEffect(() => {
    const user = authService.getCurrentUser();
    if (!user) {
      // –ï—Å–ª–∏ –Ω–µ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω - –ø–æ–∫–∞–∑—ã–≤–∞–µ–º —Å–æ–æ–±—â–µ–Ω–∏–µ
      return;
    }
    setCurrentUser(user);
    loadPosts();
  }, []);

  // –ï—Å–ª–∏ –Ω–µ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω
  if (!currentUser) {
    return (
      <div className="auth-required">
        <div className="auth-message">
          <h1>üîê –¢—Ä–µ–±—É–µ—Ç—Å—è –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è</h1>
          <p>–ß—Ç–æ–±—ã –ø–∏—Å–∞—Ç—å –∏ —á–∏—Ç–∞—Ç—å –ø–æ—Å—Ç—ã, –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</p>
          <div className="auth-buttons">
            <Link href="/" className="auth-button primary">
              üìù –ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è
            </Link>
            <Link href="/" className="auth-button secondary">
              ‚Üê –ù–∞ –≥–ª–∞–≤–Ω—É—é
            </Link>
          </div>
        </div>
      </div>
    );
  }

  // –ó–∞–≥—Ä—É–∑–∫–∞ –ø–æ—Å—Ç–æ–≤
  const loadPosts = async (tag?: string) => {
    setLoading(true);
    try {
      const data = await blogApi.getPosts(tag);
      setPosts(data);
    } catch (error) {
      console.error('–û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏ –ø–æ—Å—Ç–æ–≤:', error);
    }
    setLoading(false);
  };

  // –î–æ–±–∞–≤–∏—Ç—å —Ç–µ–≥
  const addTag = () => {
    const tag = newPost.tagInput.trim().toLowerCase();
    if (tag && !tags.includes(tag)) {
      setTags([...tags, tag]);
      setNewPost({...newPost, tagInput: ''});
    }
  };

  // –£–¥–∞–ª–∏—Ç—å —Ç–µ–≥
  const removeTag = (tagToRemove: string) => {
    setTags(tags.filter(tag => tag !== tagToRemove));
  };

  // –°–æ–∑–¥–∞—Ç—å –ø–æ—Å—Ç
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!newPost.title.trim() || !newPost.content.trim()) {
      alert('–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –∑–∞–≥–æ–ª–æ–≤–æ–∫ –∏ —Å–æ–¥–µ—Ä–∂–∞–Ω–∏–µ');
      return;
    }
    
    setSubmitting(true);
    try {
      const createdPost = await blogApi.createPost(
        newPost.title,
        newPost.content,
        currentUser.id,
        currentUser.name,
        tags
      );
      
      if (createdPost) {
        setPosts([createdPost, ...posts]);
        setNewPost({ title: '', content: '', tagInput: '' });
        setTags([]);
        alert('–ü–æ—Å—Ç —É—Å–ø–µ—à–Ω–æ –æ–ø—É–±–ª–∏–∫–æ–≤–∞–Ω!');
      }
    } catch (error) {
      console.error('–û—à–∏–±–∫–∞ —Å–æ–∑–¥–∞–Ω–∏—è –ø–æ—Å—Ç–∞:', error);
      alert('–ù–µ —É–¥–∞–ª–æ—Å—å —Å–æ–∑–¥–∞—Ç—å –ø–æ—Å—Ç');
    }
    setSubmitting(false);
  };

  // –ü–æ–∏—Å–∫ –ø–æ —Ç–µ–≥—É
  const handleTagSearch = (tag: string) => {
    setSearchTag(tag);
    loadPosts(tag);
  };

  // –°–±—Ä–æ—Å–∏—Ç—å –ø–æ–∏—Å–∫
  const clearSearch = () => {
    setSearchTag('');
    loadPosts();
  };

  // –õ–∞–π–∫ –ø–æ—Å—Ç–∞
  const handleLike = async (postId: number) => {
    try {
      const updatedPost = await blogApi.likePost(postId);
      if (updatedPost) {
        setPosts(posts.map(post => 
          post.id === postId 
            ? { ...post, likes_count: updatedPost.likes_count }
            : post
        ));
      }
    } catch (error) {
      console.error('–û—à–∏–±–∫–∞:', error);
    }
  };

  // –í—ã–π—Ç–∏
  const handleLogout = () => {
    authService.logout();
    router.push('/');
  };

  // –§–æ—Ä–º–∞—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ –¥–∞—Ç—ã
  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('ru-RU', {
      day: 'numeric',
      month: 'long',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  return (
    <div className="blog-container">
      <header className="blog-header">
        <div className="user-info">
          <h1>üìù –ë–ª–æ–≥ —Å–æ–æ–±—â–µ—Å—Ç–≤–∞</h1>
          <p>–í—ã –≤–æ—à–ª–∏ –∫–∞–∫: <strong>{currentUser.name}</strong></p>
        </div>
        <div className="header-buttons">
          <Link href="/" className="header-button">
            ‚Üê –ù–∞ –≥–ª–∞–≤–Ω—É—é
          </Link>
          <button onClick={handleLogout} className="logout-button">
            –í—ã–π—Ç–∏
          </button>
        </div>
      </header>

      <div className="blog-main">
        {/* –õ–µ–≤–∞—è —á–∞—Å—Ç—å - —Ñ–æ—Ä–º–∞ */}
        <div className="form-section">
          <h2>‚úçÔ∏è –ù–æ–≤—ã–π –ø–æ—Å—Ç –æ—Ç {currentUser.name}</h2>
          
          {/* –ü–æ–∏—Å–∫ –ø–æ —Ç–µ–≥–∞–º */}
          <div className="search-box">
            <input
              type="text"
              placeholder="–ü–æ–∏—Å–∫ –ø–æ —Ç–µ–≥—É..."
              value={searchTag}
              onChange={(e) => setSearchTag(e.target.value)}
              className="search-input"
            />
            <button 
              onClick={() => handleTagSearch(searchTag)}
              className="search-btn"
            >
              –ù–∞–π—Ç–∏
            </button>
            {searchTag && (
              <button onClick={clearSearch} className="clear-btn">
                ‚úï
              </button>
            )}
          </div>

          {/* –§–æ—Ä–º–∞ —Å–æ–∑–¥–∞–Ω–∏—è –ø–æ—Å—Ç–∞ */}
          <form onSubmit={handleSubmit} className="post-form">
            <input
              type="text"
              placeholder="–ó–∞–≥–æ–ª–æ–≤–æ–∫ –ø–æ—Å—Ç–∞"
              value={newPost.title}
              onChange={(e) => setNewPost({...newPost, title: e.target.value})}
              className="form-input"
              required
            />
            
            <textarea
              placeholder="–°–æ–¥–µ—Ä–∂–∞–Ω–∏–µ –ø–æ—Å—Ç–∞... –ò—Å–ø–æ–ª—å–∑—É–π—Ç–µ #—Ö–µ—à—Ç–µ–≥–∏"
              value={newPost.content}
              onChange={(e) => setNewPost({...newPost, content: e.target.value})}
              className="form-textarea"
              rows={5}
              required
            />
            
            {/* –í–≤–æ–¥ —Ç–µ–≥–æ–≤ */}
            <div className="tags-input">
              <input
                type="text"
                placeholder="–î–æ–±–∞–≤–∏—Ç—å —Ç–µ–≥"
                value={newPost.tagInput}
                onChange={(e) => setNewPost({...newPost, tagInput: e.target.value})}
                onKeyDown={(e) => e.key === 'Enter' && (e.preventDefault(), addTag())}
                className="tag-input"
              />
              <button type="button" onClick={addTag} className="add-tag-btn">
                +
              </button>
            </div>
            
            {/* –í—ã–±—Ä–∞–Ω–Ω—ã–µ —Ç–µ–≥–∏ */}
            {tags.length > 0 && (
              <div className="selected-tags">
                {tags.map(tag => (
                  <span key={tag} className="tag">
                    #{tag}
                    <button 
                      type="button"
                      onClick={() => removeTag(tag)}
                      className="remove-tag"
                    >
                      √ó
                    </button>
                  </span>
                ))}
              </div>
            )}
            
            <button type="submit" disabled={submitting} className="submit-btn">
              {submitting ? '–ü—É–±–ª–∏–∫–∞—Ü–∏—è...' : 'üì§ –û–ø—É–±–ª–∏–∫–æ–≤–∞—Ç—å –ø–æ—Å—Ç'}
            </button>
          </form>
        </div>

        {/* –ü—Ä–∞–≤–∞—è —á–∞—Å—Ç—å - –ø–æ—Å—Ç—ã */}
        <div className="posts-section">
          <h2>
            {searchTag ? `–ü–æ—Å—Ç—ã —Å —Ç–µ–≥–æ–º: #${searchTag}` : 'üì∞ –í—Å–µ –ø–æ—Å—Ç—ã'}
            <span className="count"> ({posts.length})</span>
          </h2>
          
          {loading ? (
            <p className="loading">–ó–∞–≥—Ä—É–∑–∫–∞ –ø–æ—Å—Ç–æ–≤...</p>
          ) : posts.length === 0 ? (
            <p className="no-posts">
              {searchTag ? `–ù–µ—Ç –ø–æ—Å—Ç–æ–≤ —Å —Ç–µ–≥–æ–º #${searchTag}` : '–ü–æ–∫–∞ –Ω–µ—Ç –ø–æ—Å—Ç–æ–≤. –ë—É–¥—å—Ç–µ –ø–µ—Ä–≤—ã–º!'}
            </p>
          ) : (
            <div className="posts-list">
              {posts.map(post => (
                <div key={post.id} className="post-card">
                  <div className="post-header">
                    <h3>{post.title}</h3>
                    <button 
                      onClick={() => handleLike(post.id)}
                      className="like-btn"
                      title="–ü–æ—Å—Ç–∞–≤–∏—Ç—å –ª–∞–π–∫"
                    >
                      ‚ù§Ô∏è {post.likes_count || 0}
                    </button>
                  </div>
                  
                  <div className="post-meta">
                    <span className="author">
                      üë§ {post.author_name}
                      {post.user_id === currentUser.id && (
                        <span className="my-post"> (–í–∞—à –ø–æ—Å—Ç)</span>
                      )}
                    </span>
                    <span className="date">
                      üìÖ {formatDate(post.created_at)}
                    </span>
                  </div>
                  
                  {/* –¢–µ–≥–∏ –ø–æ—Å—Ç–∞ */}
                  {post.tags && post.tags.length > 0 && (
                    <div className="post-tags">
                      {post.tags.map(tag => (
                        <button
                          key={tag}
                          onClick={() => handleTagSearch(tag)}
                          className="post-tag"
                        >
                          #{tag}
                        </button>
                      ))}
                    </div>
                  )}
                  
                  <div className="post-content">
                    {post.content.split(' ').map((word, idx) => 
                      word.startsWith('#') ? (
                        <button
                          key={idx}
                          onClick={() => handleTagSearch(word.slice(1))}
                          className="hashtag-in-text"
                        >
                          {word}
                        </button>
                      ) : (
                        <span key={idx}> {word} </span>
                      )
                    )}
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}